body{
    background-color: #222222;
    color: white;
}

.done{
    text-decoration: line-through;
}

.done{
    opacity: 0.7;
}

.card{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    margin-top: 15px;
    box-shadow: 0px 2px 7px 1px rgba(0, 0, 0, 0.2);
    background-color: #007b9d;


}

.container{
    max-width: 600px;
    margin: auto;
}

ul{
    list-style: none;
    padding:0;
    margin: 0;
}

button{
    cursor: pointer;
}

.modal{
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.2);
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal .card{
    background-color: white;
}

.hide{
    display: none;
}

.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.addButton{
    width: 45px;
    height: 45px;
    border-radius: 50%;
    font-size: 1.5rem;
    border: none;
    background-color: #089dc6;
    color: white;
    font-weight: bolder;
    transition: 0.1s;
    border: solid 5px #089dc6;

}

.addButton:active{
    background-color: white;
    color: #007b9d;
    border: solid 5px white;
}

.addButton:hover{
    transition: 0.1s;
    background-color: white;
    color: #007b9d;
    border-radius: 40%;
    border-color: #089dc6;
}

.delButton{
    transition: 0.15s;
    background-color: red;
    color: white;
    border: none;
    font-weight: bolder;
}

.delButton:hover{
    transition: 0.15s;
    background-color: white;
    color: red;
}

.checkButton{
    border: none;
    background-color: green;
    color: white;
    font-weight: bolder;
}

.checkButton:hover{
    background-color: white;
    color: green;
}
span{
    font-size: 10px;
}

.button{
    width: 70px;
    padding: 7.5px;
    margin-left: 7.5px;
    border-radius: 8px;
}
